import React, {useEffect, useState} from 'react';
import MealItem from './MealItem/MealItem'
import classes from './AvailableMeals.module.css'
import Card from '../UI/Card';




const AvailableMeals = () => {
  const [meals, setMeals] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [httpError, setHttpError] = useState(null)


  useEffect(() => {
    const fetchMeals = async () => {
      const response = await fetch('https://react-b2096-default-rtdb.firebaseio.com/food.json')
      
      if(!response.ok) {
        throw new Error('Something went wrong')
      }
      
      const responseData = await response.json()
      const loadedMeals = []
     
     
      for (const key in responseData) {
        loadedMeals.push({
          id: key,
          name: responseData[key].name,
          description: responseData[key].description,
          price: responseData[key].price,
        })
      }
      setMeals(loadedMeals)
      setIsLoading(false)
    } 
    
      fetchMeals().catch(error => {
        setIsLoading(false)
        setHttpError(error.message)
      })
    
     
  }, [])
    

    if(isLoading) {
      return <section className={classes.MealsLoading}>
        <p>Loading...</p>
      </section>
    }


    if(httpError) {
      return <section className={classes.MealsError}>
        <p>{httpError}</p>
      </section>
    }

    const mealList = meals.map(meal => <MealItem key={meal.id} id={meal.id} name={meal.name} description={meal.description} price={meal.price} />)
    
    
    
    return (
        <section className={classes.meals}>
            <Card>
              <ul>
                  {mealList}
              </ul>
            </Card>
        </section>
    );
};

export default AvailableMeals;